body {
  margin: 0;
  font-family: "Sawarabi Mincho", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  line-height: 1.3;
  color: #000000; }

:focus {
  outline: none; }

a:link,
a:visited {
  color: #000;
  text-decoration: underline; }

h1 {
  font-size: 100px;
  width: 100%;
  letter-spacing: 0.1em;
  font-weight: bold;
  line-height: 1;
  margin: 40px auto 20px;
  text-align: center; }
  h1.main-head {
    letter-spacing: -2px;
    line-height: 1em;
    margin-top: 0;
    margin-bottom: 50px;
    text-decoration: underline; }

h2,
h3 {
  font-family: "Playfair Display", serif;
  font-weight: 900; }

.main-scene {
  width: 100%;
  max-width: 850px;
  margin: 0 auto;
  background: #ffffff; }

section {
  position: relative;
  padding: 20px;
  padding-bottom: 30px; }
  section:after {
    width: 90vw;
    background: #000;
    height: 1px;
    content: "";
    bottom: 0;
    display: inline-block;
    position: absolute; }

.flight-info {
  width: 100%;
  display: flex;
  align-content: center;
  -webkit-align-content: center;
  -ms-align-items: center;
  align-items: center;
  margin: 0px auto 50px; }
  .flight-info h1 {
    display: inline-block;
    position: relative;
    width: auto;
    margin: 0 50px 0 0;
    font-size: 80px; }
    .flight-info h1:after {
      position: absolute;
      content: "";
      background: #000;
      height: 1.6em;
      width: 2px;
      top: 20px;
      right: -4px;
      -webkit-transform: rotate(20deg);
      transform: rotate(20deg); }

.slash-end {
  display: inline-block;
  position: relative;
  width: auto;
  font-size: 20px;
  margin-right: 30px; }
  .slash-end:after {
    position: absolute;
    content: "";
    background: #000;
    height: 23px;
    width: 1px;
    top: 5px;
    right: -10px;
    -webkit-transform: rotate(20deg);
    transform: rotate(20deg); }

.full {
  width: 100%;
  margin: 0px auto 5px; }

.time-wrapper {
  display: flex;
  flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  margin-bottom: 30px; }
  .time-wrapper .time-mark {
    position: relative;
    padding-left: 45px;
    line-height: 35px;
    font-size: 40px;
    letter-spacing: -5px; }
    .time-wrapper .time-mark:before {
      display: block;
      position: absolute;
      left: 0px;
      top: 0;
      content: "";
      background: #000;
      width: 35px;
      height: 35px;
      border-radius: 50%; }

.time-detail {
  width: 100%;
  padding-left: 25px; }
  .time-detail li {
    margin-bottom: 20px; }
  .time-detail .slash-end {
    font-size: 16px; }
  .time-detail .full {
    font-size: 14px; }

.place-target {
  font-size: 18px;
  margin: 16px 0; }
  .place-target span {
    display: inline-block;
    position: relative;
    padding-right: 20px; }
    .place-target span:after {
      position: absolute;
      display: block;
      width: 15px;
      height: 1px;
      background: #000;
      right: 0.5px;
      top: 10px;
      content: ""; }
    .place-target span:last-child:after {
      display: none; }

.remark {
  font-size: 0.85em;
  color: #c70025; }

.map {
  width: 100%;
  height: 350px;
  position: relative; }

.img-wrapper {
  display: flex;
  flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  justify-content: space-around;
  object-position: cover;
  margin: 15px 0; }
  .img-wrapper img {
    width: 45%; }

